body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
}

#joystick-circle {
  position: absolute;
  background: lightgray;
  border-radius: 50%;
  opacity: 0.5;
}

#joystick-stick {
  position: absolute;
  background: gray;
  border-radius: 50%;
}

#aim-area {
  position: absolute;
  background: lightgray;
  border-radius: 50%;
  opacity: 0.3;
}

#aim-dot {
  position: absolute;
  background: black;
  border-radius: 50%;
}

#aim-projectile {
  position: absolute;
  background: red;
  border-radius: 50%;
}

#center-dot {
  position: absolute;
  background: white;
  border-radius: 50%;
}
